html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
}

a {
  text-decoration: none;
}

a:active,
a:hover {
  outline: 0;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus {
  outline: 0;
}

img,
audio,
video {
  max-width: 100%;
  height: auto;
}

audio,
canvas,
iframe,
video,
img,
svg {
  vertical-align: middle;
}

iframe {
  border: 0;
}

textarea {
  resize: none;
  /* remove the resize handle on the bottom right */
  overflow: auto;
  vertical-align: top;
  box-shadow: none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
}

input,
textarea,
select,
button {
  outline: none;
  border: none;
  font-size: 100%;
  margin: 0;
}

button,
input {
  line-height: normal;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
  text-align: left;
}

html,
body {
  font: 400 10px/1.5 Ubuntu, Helvetica, Arial, sans-serif;
  color: #313233;
}

body,
input,
textarea,
button {
  font-family: 'El Messiri', sans-serif;
  text-align: center;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.7rem;
}

h3 {
  font-size: 1.45rem;
}

h4 {
  font-size: 1.3rem;
}

h5 {
  font-size: 1.15rem;
}

p,
li,
em,
b,
a {
  font-size: 1rem;
}

h1,
h2,
h3,
h4,
h5,
p {
  margin: 0;
}

h2,
h3 {
  margin: 1em 0;
}

a,
a:hover {
  text-decoration: none;
}

.container {
  max-width: 90vw;
  margin: 0 auto;
}

.iconcards {
  font-size: 2rem;
}

@media (min-width: 320px) {
  html {
    font-size: 14px;
  }
}

@media (min-width: 480px) {
  html {
    font-size: 16px;
  }
}

@media (min-width: 640px) {
  html {
    font-size: 16.5px;
  }
}

@media (min-width: 768px) {
  html {
    font-size: 17px;
  }
}

@media (min-width: 992px) {
  html {
    font-size: 17.5px;
  }
}

@media (min-width: 1200px) {
  html {
    font-size: 18.5px;
  }
}

@media (min-width: 1920px) {
  html {
    font-size: 20px;
  }
}

@media (min-width: 2560px) {
  html {
    font-size: 22px;
  }
}

@media (min-width: 3060px) {
  html {
    font-size: 27.5px;
  }
}

@media (min-width: 1440px) {
  .container {
    width: 1410px;
  }
}

@media (min-width: 1920px) {
  .container {
    width: 1800px;
  }
}

@media (min-width: 2560px) {
  .container {
    width: 2400px;
  }
}